import { LitElement, html, css } from "lit-element";

/* TOOLS */
export class Wrapper extends LitElement {
  firstUpdated(){
    global.routes = [];
    let insideWrapper = this.shadowRoot.host.children;
    for (var i = 0; i < insideWrapper.length; i++) {
      global.routes.push(insideWrapper[i].path);
    }
    let showing = false;
    for (var j = 0; j < global.routes.length; j++) {
      if(window.location.pathname === global.routes[j]){
        showing = true;
        break;
      }
    }
    if(!showing){
      if(window.location.pathname !== "/404"){
        window.location.href = "/404";
      }
    }
  }
  render() {
    return html`<slot />`;
  }
}

export class Page extends LitElement {
  static get properties() {
    return {
      path: { type: String }
    };
  }

  constructor() {
    super();
    this.path = "/";
  }

  static get styles() {
    return css`
      div {
        position: absolute;
        left: 56.5%;
        top: 10%;
        transform: translateX(-50%);
        width: 85%;
        padding-bottom: 2.5%;
      }
    `;
  }

  /*firstUpdated(){
    global.routes = [];
    for (var i = 0; i < this.shadowRoot.host.children.length; i++) {
      if (this.shadowRoot.host.children[i].path) {
        global.routes.push(this.shadowRoot.host.children[i].path);
      }
    }
    console.log(this.shadowRoot.host.children);
  }*/
  render() {
    if (window.location.pathname === this.path) {
      return html`<div><slot /></div>`;
    } else {
      return null;
    }
  }
}

/* STYLED ELEMENTS */
export class Title extends LitElement {
  static get styles() {
    return css`
      .title {
        width: 85%;
      }

      @media only screen and (max-width: 600px) {
        .title {
          font-weight: 700;
          font-size: 5vmax;
        }
      }

      @media only screen and (min-width: 600px) {
        .title {
          font-weight: 700;
          font-size: 3vmax;
        }
      }
    `;
  }

  render() {
    return html`<div class="title"><slot /></div>`;
  }
}

export class Body extends LitElement {
  static get styles() {
    return css`
      .body {
        width: 85%;
        overflow-x: auto;
        text-align: justify;
      }
    `;
  }

  render() {
    return html`<div class="body"><br/><slot /></div>`;
  }
}

export class Author extends LitElement {
  static get styles() {
    return css`
      @media only screen and (max-width: 600px) {
        .author {
          font-weight: 500;
          color: #696969;
          font-size: 2.15vmax;
        }
      }

      @media only screen and (min-width: 600px) {
        .author {
          font-weight: 500;
          color: #696969;
          font-size: 1.25vmax;
        }
      }
    `;
  }

  render() {
    return html`<span class="author">— <slot /></span>`;
  }
}

export class Date extends LitElement {
  static get styles() {
    return css`
      @media only screen and (max-width: 600px) {
        .date {
          font-weight: 500;
          color: #696969;
          font-size: 2.15vmax;
        }
      }

      @media only screen and (min-width: 600px) {
        .date {
          font-weight: 500;
          color: #696969;
          font-size: 1.25vmax;
        }
      }
    `;
  }

  render() {
    return html`<span class="date"><slot /></span>`;
  }
}

customElements.get("skriv-app") ||
  customElements.define("skriv-app", Wrapper);
customElements.get("skriv-page") ||
  customElements.define("skriv-page", Page);
customElements.get("skriv-title") ||
  customElements.define("skriv-title", Title);
customElements.get("skriv-author") ||
  customElements.define("skriv-author", Author);
customElements.get("skriv-date") ||
  customElements.define("skriv-date", Date);
customElements.get("skriv-body") ||
  customElements.define("skriv-body", Body);
